import React, { useState, useEffect } from 'react';
import { Outlet, useLocation, useNavigate, Link } from "react-router-dom";
import { Layout, Menu, Drawer, theme, Avatar } from 'antd';
import { MenuOutlined, UserOutlined, CodeOutlined, LogoutOutlined } from "@ant-design/icons";
import "./index.css"
import logo from "../../images/immsmart_logo_24.png"

const { Header, Content, Footer } = Layout;
// const items = new Array(3).fill(null).map((_, index) => ({
//   key: String(index + 1),
//   label: `nav ${index + 1}`,
// }));


function MyLayout(){
  const location = useLocation();
  const navigate = useNavigate();
  const [isLogin, setIsLogin] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);

  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };


  useEffect(() => {
    setIsLogin(true);
    if (location.pathname === "/login" || location.pathname === "/register") {
      setIsLogin(false);
    }
  }, [location.pathname]);

  const handleHome = () => {
    navigate('/');
    setOpenMenu(false);
  }

  const handleAbout = () => {
    navigate('/');
    setOpenMenu(false);
    scrollToSection("#about")
    
  }

  const handleFind = () => {
    navigate("/consultants");
    setOpenMenu(false);
  }

  const handleForum = () => {
    navigate("/forum");
    setOpenMenu(false);
  }

  const handleProfile = () => {
    navigate("/personal");
    setOpenMenu(false);
  }
  
  const handleLogout = () => {
    setIsLogin(false);
    setOpenMenu(false);
  };

  const handleLogin = () => {
    // setIsLogin(true);
    navigate('/login');
    setOpenMenu(false);
  }

  const handleSignUp = () => {
    navigate('/register');
    setOpenMenu(false);

  }

  const handleMessage = () => {
    navigate('/message');
    setOpenMenu(false);
    
  }
  const AppMenu = ({ isInline = false })=>{
    return (
      <Menu
        theme="dark"
        mode={isInline ? "inline":"horizontal"}
        // defaultSelectedKeys={['find']}
        // items={items}
        style={{
          flex: "auto",
          minWidth: 0,
          paddingLeft: "24px",
          // justifyContent: 'space-evenly'
        }}
      >
        <Menu.Item key="home" onClick={handleHome}>Home</Menu.Item>
        <Menu.Item key="about" onClick={handleAbout}>About Us</Menu.Item>
        <Menu.Item key="find" onClick={handleFind}>Find Your Immigration Consultant</Menu.Item>
        <Menu.Item key="forum" onClick={handleForum}>Forum</Menu.Item>
        {/* {console.log(isLogin)} */}
        {isLogin ? (
          <Menu.SubMenu
            title={
              <>
                <Avatar icon={<UserOutlined />} />
                <span className="username">John Doe</span>
              </>
            }
            key="user"
          >
            <Menu.Item key="profile" onClick={handleProfile}>
              <CodeOutlined /> Profile
            </Menu.Item>
            <Menu.Item key="message" onClick={handleMessage}>
              <UserOutlined /> Message
            </Menu.Item>
            <Menu.Item key="log-out" onClick={handleLogout}>
              <LogoutOutlined /> Logout
            </Menu.Item>
          </Menu.SubMenu>
          ) : (
            <>
            <Menu.Item key="login" onClick={handleLogin}>
              Login
            </Menu.Item>
            <Menu.Item key="signup" onClick={handleSignUp}>
              Sign Up
            </Menu.Item>
            </>
        )}
      </Menu>
    );
  }

  return (
    <Layout>
      <Header
        style={{
          position: 'sticky',
          top: 0,
          zIndex: 1,
          width: '100%',
          // display: 'flex',
          // alignItems: 'center',
        }}
      > 
        <div className='menuIcon'>
          <div className="logo" >
            <img src={logo} alt="" style={{ height: "64px", display:"block", marginLeft:"auto", marginRight:"auto" }} />
          </div>
            <MenuOutlined 
              style={{color:"white", fontSize:24}} 
              onClick={() => {
                setOpenMenu(true);
              }}
            />
        </div>
        
        <div className='headerMenu'>
          <div className="logo">
            <img src={logo} alt="" style={{ height: "64px", display:"block", marginLeft:"auto", marginRight:"auto" }} />
          </div>
          <AppMenu />
        </div>
        <Drawer 
          placement='left'
          open={openMenu}
          onClose={()=>{
            setOpenMenu(false);
          }}
          closable={false}
        >
          <AppMenu isInline={true}/>
        </Drawer>
      </Header>
      <Content
        style={{
          padding: '0 0px',
        }}
      >
        <Outlet />
      </Content>
      <Footer
        style={{
          textAlign: 'center',
        }}
      >
        Ant Design 2023 Created by Ant UED
      </Footer>
    </Layout>
  );
};



export default MyLayout;