import React from 'react';
import './App.css';
// import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import MyLayout from './components/MyLayout';

// import Test from './components/Test';
import Home from './components/Home';
import Consultants from './components/Consultants';
import ConsultantDetails from './components/Consultants/details';
import Bookmeeting from './components/Bookmeeting';
import Payment from './components/Payment';
import Login from './components/Login';
import Register from './components/Register';
import Message from './components/Message';
import Personal from './components/Personal';
import Vip from './components/Personal/vip';
import Forum from './components/Forum';
import Bbs from './components/Forum/bbs';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<MyLayout />}>
          <Route index element={<Home />}></Route>
          <Route path="/consultants" element={<Consultants />}></Route>
          <Route path="/consultants/detail/:id" element={<ConsultantDetails />}></Route>
          <Route path="/booking/:id" element={<Bookmeeting />}></Route>
          <Route path="/payment" element={<Payment />}></Route>
          <Route path="/login" element={<Login />}></Route>
          <Route path="/register" element={<Register />}></Route>
          <Route path="/message" element={<Message />}></Route>
          <Route path="/personal" element={<Personal />}></Route>
          <Route path="/vip" element={<Vip />}></Route>
          <Route path="/forum" element={<Forum />}></Route>
          <Route path="/forum/bbs" element={<Bbs />}></Route>
          {/* <Route path="/#about-us" element={<Home/>}></Route> */}


          
        </Route>
      </Routes>
    </Router>
  );
}

export default App;
