import React, { useState } from "react";
import {
  Button,
  Divider,
  Avatar,
  Form,
  Input,
  Alert,
  Space,
  FloatButton,
  Row,
  Col
} from "antd";
import {
  UserOutlined,
  GlobalOutlined,
  MailOutlined,
  PayCircleOutlined,
} from "@ant-design/icons";
// import "./index.scss";
import { useNavigate } from "react-router-dom";


export default function Personal() {
  const navigate = useNavigate();
  const [userType, setUserType] = useState(1);
  const [need, setNeed] = useState("");
  const [userInfo, setUserInfo] = useState({
    name: "Rose",
    country: "",
    email: "xxxx@exam.com",
    needs: ["1", "2", "3"],
    intro:
      "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Sapiente consequatur consectetur quae ad sunt. Magnam, id veniam ipsam quo numquam necessitatibus. Suscipit totam odio necessitatibus ab, rerum quo nulla iusto.",
  });
  const [userInfo2, setUserInfo2] = useState({
    name: "Rose",
    country: "",
    email: "xxxx@exam.com",
    ciccid: "R12345678",
    needs: ["1", "2", "3"],
    intro:
      "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Sapiente consequatur consectetur quae ad sunt. Magnam, id veniam ipsam quo numquam necessitatibus. Suscipit totam odio necessitatibus ab, rerum quo nulla iusto.",
  });
  const onFinish = (values) => {
    console.log(values);
  };
  const handleChange = (e) => {
    setNeed(e.target.value);
  };
  const handleAdd = () => {
    if (!need) {
      return;
    }
    setUserInfo({ needs: [need, ...userInfo.needs] });
    setNeed("");
  };
  const handleClose = (n) => {
    console.log(n);
    const needs = userInfo.needs.filter((need) => need !== n);
    setUserInfo({ needs });
  };
  const handleVip = (e) => {
    navigate(`/vip`)
  };
  return (
    <div style={{padding:"48px"}}>
            <Row justify={"center"} align={"top"} gutter={{
                  xs: 8,
                  sm: 16,
                  md: 24, 
                  lg: 32,
              }}>
                <Col sm={24} md={10} lg={10}>
                    <div style={{textAlign:"center"}}>
                        <Avatar
                            src={
                                "https://www.cedarhill.io/wp-content/uploads/2022/04/ds-website.jpg"
                            }
                            size={{
                                xl: 180,
                                xxl: 180,
                                lg: 160,
                                md: 180,
                                sm: 180,
                                xs: 300,
                            }}
                        ></Avatar>
                    </div>
              </Col>
            <Col sm={24} md={14} lg={14}>
         
            <div>
              {userType === 2 ? (
                // 
                <Form onFinish={onFinish} initialValues={userInfo}>
                  <Divider orientation="left"></Divider>
                  <Form.Item name="name">
                    <Input prefix={<UserOutlined />} />
                  </Form.Item>
                  <Form.Item name="country">
                    <Input prefix={<GlobalOutlined />} />
                  </Form.Item>
                  <Form.Item name="email">
                    <Input prefix={<MailOutlined />} />
                  </Form.Item>
                  <Divider orientation="left"></Divider>
                  <Space.Compact style={{ width: "100%" }}>
                    <Input
                      placeholder=""
                      className="mb-3"
                      value={need}
                      onChange={(e) => handleChange(e)}
                    ></Input>
                    <Button type="primary" onClick={() => handleAdd()}>
                      +
                    </Button>
                  </Space.Compact>
                  {userInfo.needs.map((need, index) => (
                    <Alert
                      closable
                      message={index + 1 + ". " + need}
                      type="info"
                      key={need}
                      className="mb-3"
                      onClose={(e) => handleClose(need)}
                    />
                  ))}
                  <Form.Item className="text-end mt-5">
                    <Button type="primary" htmlType="submit" className="w-md-100">
                      
                    </Button>
                  </Form.Item>
                </Form>
              ) : (
                // 
                <Form onFinish={onFinish} initialValues={userInfo2}>
                  <Divider orientation="left"></Divider>
                  <Form.Item name="name">
                    <Input prefix={<UserOutlined />} />
                  </Form.Item>
                  <Form.Item name="country">
                    <Input prefix={<GlobalOutlined />} />
                  </Form.Item>
                  <Form.Item name="email">
                    <Input prefix={<MailOutlined />} />
                  </Form.Item>

                  <Divider orientation="left"></Divider>
                  <Space.Compact style={{ width: "100%" }}>
                    <Input
                      placeholder=""
                      className="mb-3"
                      value={need}
                      onChange={(e) => handleChange(e)}
                    ></Input>
                    <Button type="primary" onClick={() => handleAdd()}>
                      +
                    </Button>
                  </Space.Compact>
                  {userInfo.needs.map((need, index) => (
                    <Alert
                      closable
                      message={index + 1 + ". " + need}
                      type="info"
                      key={need}
                      className="mb-3"
                      onClose={(e) => handleClose(need)}
                    />
                  ))}
                  <Divider orientation="left"></Divider>
                  <Form.Item name="intro">
                    <Input.TextArea
                      showCount
                      maxLength={270}
                      style={{ resize: "none", height: 120 }}
                      value={userInfo2.intro}
                    ></Input.TextArea>
                  </Form.Item>
                  <Form.Item className="text-end mt-5">
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="w-md-100"
                    >
                      
                    </Button>
                  </Form.Item>
                </Form>
              )}
              <div className="fixed-button">
                <Button
                  size="large"
                  type="primary"
                  icon={<PayCircleOutlined />}
                  onClick={()=>handleVip()}
                >
                  
                </Button>
              </div>
            </div>
            </Col>
        </Row>
    </div>
  );
}
